main {
	margin: 0 auto;
	max-width: 90%;
	padding: 50px 30px;
}

:root {
	--primary: #499167;
	--secondary: #332c2e;
	--tertiary: #f0f2a6;
	--quaternary: #a191a3;
	--quinary: #bfd7ea;
	--light: #bfd7ea;
	--dark: #616a82;
	--darker: #39a9db;
	--error: rgb(228, 46, 1);
	--spacing-one: calc(1rem * 0.5);
	--spacing-two: calc(1rem);
	--spacing-five: calc(2.5rem);
}

* {
	box-sizing: border-box;
}

div {
  display: block;
}

html,
body {
	min-height: 100%;
}
body {
  margin: 0;
  font-weight: 600;
	line-height: 1.5;
	font-size: 18px;
	letter-spacing: .1em;
	background: var(--dark);
	color: var(--light);
	/* font-family: 'Poiret One', cursive; */
	font-family: 'Courier New', Courier, monospace;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1 {
	font-size: 2.25rem;
  padding: 20px;
}

h2 {
	font-size: 2rem;
}

h3 {
	font-size: 1.75rem;
}

h4 {
	font-size: 1.5rem;
}

h5 {
	font-size: 1.25rem;
}

h6 {
	font-size: 1rem;
}

p {
	font-size: 1.1rem;
	margin-top: 0;
  padding: 20px;
}

a {
	color: rgb(221, 195, 227);
	text-decoration: none;
	font-weight: bold;
	border-radius: 5px;
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
}

a:hover {
	text-decoration: underline;
}
ul{
  display:flex;
  flex-wrap: wrap;
}
ul li {
	list-style: none;
  padding: 3px;

}

ol li {
	list-style: inside;
}

nav a:hover {
	text-decoration: none;
  opacity: .4;
}

nav {
	background: var(--secondary);
	display: flex;
	justify-content: space-between;
	padding: 1rem;
	text-align: center;
}

nav a {
	color: var(--light);
}

nav li {
	cursor: pointer;
  list-style: none;
}

.page-center {
	 
  display: flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.page-header {
	font-family: 'Roboto', sans-serif;
  display: flex;
	font-size: 2.5em;
	font-weight: 500;
	margin: 5px 0;
	line-height: 1.25;
	padding-bottom: 2rem;
}

.resume {
  display: flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.photo {
	width: auto;
	height: auto;
	max-width: 200px;
	max-height: 300px;
	/* padding: 50px; */
	margin-bottom: 50px;
  border-radius: 30px;
}
form {
  display: flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
	 

  
}

input[type='submit'] {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
	width: 25%;
	font-size: 1rem;
	padding: 0.25rem;
	border: 2px solid var(--dark);
	outline: none;
	border-radius: 5px;
	transition: box-shadow 0.2s;

}

input[type='text'] {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border: 3px solid #d3c5c5;
	padding: 5px;
  width: 600px;

}

input[type='email'] {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border: 3px solid #d3c5c5;
	padding: 5px;
  width: 600px;

}

input:focus {
	border: 2px solid var(--secondary);
	box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}
label {
  margin-top: 1rem;

}
textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #d3c5c5;
	padding: 5px;
}

.send-button {
	background: var(--quaternary);
  text-align: center;
	border: 0;
	border-radius: .5rem;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	padding: 10px 15px;
	font-size: 1rem;

}

.send-button:hover {
	opacity: .6;
}

.send-button[disabled] {
	cursor: default;
	opacity: .8;
}

img {
	max-width: 100%;
}

span[role="img"] {
	cursor: default;
}
.image {
	width: auto;
	height: auto;
	max-width: 500px;
	max-height: 500px;
	padding: 50px;
}
.container {
	width: 85%;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
}

.flex-row {
	display: flex;
	justify-content: space-evenly;
}

.space-between {
	justify-content: space-between;
}

.flex-end {
	justify-content: flex-end;
}

.mx-1 {
	margin-right: var(--spacing-one);
	margin-left: var(--spacing-one);
}

.mx-2 {
	margin-right: var(--spacing-two);
	margin-left: var(--spacing-two);
}

.my-1 {
	margin-top: var(--spacing-one);
	margin-bottom: var(--spacing-one);
}

.my-2 {
	margin-top: var(--spacing-two);
	margin-bottom: var(--spacing-two);
}


.my-5 {
	margin-top: var(--spacing-five);
	margin-bottom: var(--spacing-five);
}

.px-1 {
	padding-right: var(--spacing-one);
	padding-left: var(--spacing-one);
}

.px-2 {
	padding-right: var(--spacing-two);
	padding-left: var(--spacing-two);
}

.py-1 {
	padding-top: var(--spacing-one);
	padding-bottom: var(--spacing-one);
}

.py-2 {
	padding-top: var(--spacing-two);
	padding-bottom: var(--spacing-two);
}

.img-thumbnail {
	width: auto;
	height: 300px;
	margin-bottom: 50px;
	cursor: pointer;
}

.modalBackdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modalContainer {
	background: var(--darker);
	border-radius: 5px;
	max-width: 100%;
	margin: 50px auto;
	padding: 15px;
	width: 960px;
}

.modalTitle {
	font-size: 18px;
	font-weight: 600;
	margin-bottom: 25px;
}

.logo {
  display: flex;
  flex-wrap: wrap;
  padding: 20 px;
  
	width: 50px;
	height: 50px;
	margin: 30px;
	cursor: pointer;
}

.logo:hover {
	opacity: 0.5;
}


.bottom-spacing {
  display: flex;
  padding: 20px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
	margin-bottom: var(--spacing-five);
}

.card {
	width: 25%;
	text-align: center;
}

.card-title {
	font-size: 1.5rem;
	color: var(--quaternary);
}

.card-subtitle {
	font-size: 1rem;
	color: var(--tertiary);
}

.card-text {
	font-size: 0.75rem;
	height: 8rem;
	color: var(--quinary);
}

.card-techs {
	font-size: 0.75rem;
	height: 4rem;
	color: var(--quinary);
}

.card-image {
	max-width: 300px;
	max-height: 150px;
}

.card-link {
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	font-size: 0.70rem;
}

.padding {
	padding: 50px;
}
footer
{
 display:flex;
 justify-content: center;
}

/* Viewports Styling start*/
@media screen and (max-width: 980px) {}

@media screen and (max-width: 817px) {
	header div {
		position: relative;
		width: 110%;
	}

	header div h2 {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}

	h2 {
		font-size: 1.5rem;
	}

	nav ul li span {
		font-size: 0.75em;
	}

	.mobile-header {
		width: 110%;
	}

	.mobile-row {
		display: block;
		margin: 0 100px 0 auto;
	}

	.mobile-view {
		display: inline;
		text-align: end;
	}

	.photo {
		width: auto;
		height: auto;
		max-width: 500px;
		max-height: 300px;
		padding: 50px;
	}

	input {
		width: 350px;
	}

	textarea {
		width: 350px;
		height: 120px;
		border: 3px solid #cccccc;
		padding: 5px;
	}

	ol {
		font-size: 0.75rem;
	}
.footer-link{
  display: flex;
  
  padding: 20px;
}

@media screen and (max-width: 768px) {}

@media screen and (max-width: 575px) {}
}
